
.page-ticket .camera .placeholder {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  background-color: #ccc;
  height: 200px;
  border-radius: 5px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.page-ticket .camera .placeholder .vux-x-icon {
    fill: #fff;
}
.page-ticket .camera .placeholder a {
    color: #fff;
}
.page-ticket .btn-wrap {
  padding: 15px 0;
}
.page-ticket .card-padding {
  padding: 15px;
}
.page-ticket .rules {
  color: #666;
}
.page-ticket .rules ul {
    list-style: none;
    padding-left: 2px;
    margin-top: 4px;
}
.page-ticket .rules h4, .page-ticket .rules ul li {
    color: #666;
    font-size: .96em;
}
.page-ticket .rules .footer {
    padding: 10px 0;
}



