
.page-ecticket-list .wrapper {
  overflow: hidden;
}
.page-ecticket-list .card {
  margin-top: 20px;
  box-shadow: 2px 2px 1px rgba(204, 204, 204, 0.51);
}
.page-ecticket-list .card:before, .page-ecticket-list .card:after {
    display: none;
}
.page-ecticket-list .card .header, .page-ecticket-list .card .footer {
    padding: 15px;
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #666;
}
.page-ecticket-list .card .header:before, .page-ecticket-list .card .footer:before {
      content: " ";
      position: absolute;
      left: 0;
      right: 0;
      height: 1px;
      bottom: 0;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
}
.page-ecticket-list .card .header.footer:before, .page-ecticket-list .card .footer.footer:before {
      top: 0;
      bottom: auto;
}
.page-ecticket-list .card .header.footer .align-right, .page-ecticket-list .card .footer.footer .align-right {
      text-align: right;
      font-size: .9em;
}
.page-ecticket-list .card .content {
    padding: 15px 15px;
    color: #333;
}
.page-ecticket-list .card .content .fields .item {
      margin: 5px 0;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-align: start;
      -webkit-align-items: flex-start;
              align-items: flex-start;
      -webkit-box-pack: start;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
}



