
.page-home .wrapper {
  padding: 15px;
}
.page-home .scroll {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow-x: hidden;
  overflow-y: auto;
}
.page-home .search {
  position: fixed !important;
  z-index: 100;
}
.page-home .search .weui-search-bar {
    background-color: rgba(0, 0, 0, 0.2);
}
.page-home .search .weui-search-bar:after {
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.page-home .search form {
    background: none;
}
.page-home .group .weui-cells__title {
  color: #333;
  font-size: 16px;
}
.page-home .group .weui-cells {
  background: transparent;
}
.page-home .group .weui-cells:before, .page-home .group .weui-cells:after {
    display: none;
}
.page-home .group:last-child {
  margin-bottom: 50px;
}
.page-home .grid {
  display: box;
  display: -webkit-box;
  height: 160px;
}
.page-home .grid .grid-item {
    position: relative;
    display: block;
}
.page-home .grid .grid-item .img {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      background-size: cover;
}
.page-home .grid[rows] {
    -webkit-box-orient: horizontal;
}
.page-home .grid[cols] {
    -webkit-box-orient: vertical;
}
.page-home .grid[rows="2"] > .grid-item {
    -webkit-box-flex: 1;
}
.page-home .grid[cols="2"] > .grid-item {
    -webkit-box-flex: 1;
}
.page-home .category {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 15px 0;
  background: #fff;
}
.page-home .category .ca-item {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.page-home .category .ca-item .icon {
      width: 45px;
      height: 45px;
}
.page-home .category .ca-item .label {
      margin-top: 10px;
      color: #666;
}
.page-home .goods {
  padding: 0 10px;
  margin-bottom: 20px;
}
.page-home .goods .inner {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 0 -10px;
}
.page-home .goods .goods-item {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    box-sizing: border-box;
}
.page-home .goods .goods-item .goods-item-box {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
      padding: 10px 5px;
      box-sizing: border-box;
      background: #fff;
}
.page-home .goods .goods-item .goods-item-box .thumb {
        position: relative;
        height: 140px;
}
.page-home .goods .goods-item .goods-item-box .thumb .img {
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          width: 100%;
          background-size: cover;
}
.page-home .goods .goods-item .goods-item-box .info .title {
        margin: 10px 0;
}
.page-home .goods .goods-item .goods-item-box .info .desc {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
                align-items: center;
        -webkit-box-pack: justify;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        color: #666;
}
.page-home .goods .goods-item .goods-item-box .info .desc .cart {
          font-size: 1.2em;
          margin-right: 10px;
}



