
.page-goods .search {
  position: fixed !important;
  z-index: 100;
}
.page-goods .search .weui-search-bar {
    background-color: rgba(0, 0, 0, 0.2);
}
.page-goods .search .weui-search-bar:after {
      border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.page-goods .search form {
    background: none;
}
.page-goods .sub-header {
  position: fixed !important;
  left: 0;
  right: 0;
  top: 44px;
  height: 44px;
  padding: 0 10px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  z-index: 100;
}
.page-goods .sub-header .order {
    width: 100%;
    padding: 8px 0;
    text-align: center;
}
.page-goods .sub-header .order.up:after {
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 0;
      height: 0;
      margin: 0 5px;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-bottom: 8px solid #666;
}
.page-goods .sub-header .order.down:after {
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 0;
      height: 0;
      margin: 0 5px;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 8px solid #666;
}
.page-goods .sub-header .view-toggle {
    position: relative;
    width: 80%;
    padding: 5px 0;
    text-align: center;
    font-size: 1.2em;
}
.page-goods .sub-header .view-toggle:before {
      content: " ";
      position: absolute;
      top: 8px;
      left: 0;
      bottom: 8px;
      width: 1px;
      border-left: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
      -webkit-transform: scaleX(0.5);
              transform: scaleX(0.5);
}
.page-goods .sub-header:after {
    content: " ";
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    bottom: 0;
    border-bottom: 1px solid #D9D9D9;
    color: #D9D9D9;
    -webkit-transform-origin: 100% 0;
            transform-origin: 100% 0;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
}
.page-goods .view {
  position: absolute;
  top: 88px;
  left: 0;
  right: 0;
  bottom: 0;
}
.page-goods .view .goods {
    padding: 0 10px;
    margin-bottom: 20px;
}
.page-goods .view .goods .goods-inner {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      margin: 0 -10px;
}
.page-goods .view .goods .goods-item {
      -webkit-flex-basis: 50%;
              flex-basis: 50%;
      box-sizing: border-box;
}
.page-goods .view .goods .goods-item .goods-item-box {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
                flex-direction: column;
        padding: 10px 5px;
        box-sizing: border-box;
        background: #fff;
}
.page-goods .view .goods .goods-item .goods-item-box .thumb {
          position: relative;
          height: 140px;
}
.page-goods .view .goods .goods-item .goods-item-box .thumb .img {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            background-size: cover;
}
.page-goods .view .goods .goods-item .goods-item-box .info .title {
          margin: 10px 0;
}
.page-goods .view .goods .goods-item .goods-item-box .info .desc {
          display: -webkit-box;
          display: -webkit-flex;
          display: flex;
          -webkit-box-align: center;
          -webkit-align-items: center;
                  align-items: center;
          -webkit-box-pack: justify;
          -webkit-justify-content: space-between;
                  justify-content: space-between;
          color: #666;
}
.page-goods .view .goods .goods-item .goods-item-box .info .desc .cart {
            font-size: 1.2em;
            margin-right: 10px;
}



