
.page-ticket-list .wrapper {
  overflow: hidden;
}
.page-ticket-list .card {
  box-shadow: 2px 2px 1px rgba(204, 204, 204, 0.51);
  margin-top: 20px;
}
.page-ticket-list .card:before, .page-ticket-list .card:after {
    display: none;
}
.page-ticket-list .card .header {
    padding: 15px;
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #666;
}
.page-ticket-list .card .header:before {
      content: " ";
      position: absolute;
      left: 15px;
      right: 15px;
      height: 1px;
      bottom: 0;
      border-bottom: 1px solid #D9D9D9;
      color: #D9D9D9;
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
      -webkit-transform: scaleY(0.5);
              transform: scaleY(0.5);
}
.page-ticket-list .card .content {
    padding: 20px 15px;
    color: #666;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-box-pack: start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.page-ticket-list .card .content .thumb {
      width: 56px;
      height: 56px;
      margin-right: 15px;
      -webkit-flex-shrink: 0;
              flex-shrink: 0;
}
.page-ticket-list .card .content .thumb img {
        width: 100%;
        height: 100%;
}
.page-ticket-list .card .content .body {
      width: 100%;
}
.page-ticket-list .card .content .title {
      margin-bottom: 5px;
}
.page-ticket-list .card .content .sub {
      font-size: .9em;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-align: center;
      -webkit-align-items: center;
              align-items: center;
      -webkit-box-pack: justify;
      -webkit-justify-content: space-between;
              justify-content: space-between;
}



