
.page-category {
  display: inherit;
}
.page-category .wrapper {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    position: absolute;
    left: 0;
    top: 44px;
    right: 0;
    bottom: 0;
}
.page-category .wrapper .category {
      position: relative;
      width: 100px;
}
.page-category .wrapper .category .group .weui-cells {
        margin: 0;
        background: transparent;
}
.page-category .wrapper .category .group .weui-cells:before {
          display: none;
}
.page-category .wrapper .category .group .weui-cells .item {
          padding: 20px 15px;
}
.page-category .wrapper .category .group .weui-cells .item.active {
            background: #fff;
}
.page-category .wrapper .sub-category {
      -webkit-box-flex: 1;
      -webkit-flex: 1;
              flex: 1;
      position: relative;
      width: 100%;
      background: #fff;
}
.page-category .wrapper .sub-category .vux-divider {
        padding: 20px 10px;
}
.page-category .wrapper .sub-category .grid {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-box-align: center;
        -webkit-align-items: center;
                align-items: center;
        -webkit-box-pack: justify;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        padding: 10px;
}
.page-category .wrapper .sub-category .grid .item {
          width: 33.333%;
          margin-bottom: 15px;
          display: -webkit-box;
          display: -webkit-flex;
          display: flex;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
          -webkit-flex-direction: column;
                  flex-direction: column;
          -webkit-box-align: center;
          -webkit-align-items: center;
                  align-items: center;
          -webkit-box-pack: center;
          -webkit-justify-content: center;
                  justify-content: center;
          background-size: cover;
          background-position: center center;
          color: #666;
}
.page-category .wrapper .sub-category .grid .item:first-child {
            margin-left: 0;
}
.page-category .wrapper .sub-category .grid .item img {
            max-width: 65px;
            margin-bottom: 10px;
}
.page-category .wrapper .sub-category .grid .item .m-title {
            font-size: 12px;
}



