
.page-ecticket .checklist {
  margin: 20px 0;
}
.page-ecticket .checklist .weui-cells {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 20px 15px;
}
.page-ecticket .checklist .weui-cells .weui-cell:before, .page-ecticket .checklist .weui-cells .weui-cell:after {
      display: none;
}
.page-ecticket footer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}
.page-ecticket footer button {
    border-radius: 0;
}



