
.page-tabs .tab-view {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 50px;
}
.page-tabs .tabbar {
  max-height: 50px;
}
.page-tabs .tabbar .tabbar-item {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
}



